<template>
  <div>
    <div class="open">
        <div class="box">
          <span class="title"> 手机短信提醒: </span>
          <i-switch size="large" v-model="message" true-value="1" false-value="0" @on-change="change(0)">
            <span slot="open">开启</span>
            <span slot="close">关闭</span>
          </i-switch>
        </div>
      <div class="box">
        <span class="title"> 邮件提醒: </span>
        <i-switch size="large" v-model="email" true-value="1" false-value="0" @on-change="change(1)">
          <span slot="open">开启</span>
          <span slot="close">关闭</span>
        </i-switch>
      </div>
    </div>
        <div class="pay">
          <form>
            <p v-for="(item,key) in items">
              <span><span class="must">*</span> {{item.name}} <strong>:</strong></span>
              <input type="text" :placeholder="item.place" v-model.trim="item.modal" @focus="enter(key)" @blur="out(key)" @input="input(key)" :disabled="phoneShow" class="phoneInput">
              <strong v-if="item.show"><!--<Icon type="close-circled" color="#e46751" size="20" style="margin-right: 0.05rem"></Icon>--> {{item.error}} </strong>
              <strong v-if="item.right"> <Icon type="checkmark-circled" size="20" color="green"></Icon> </strong>
            </p>
          </form>
        </div>
        <div class="line"></div>
        <div class="pay">
          <form>
            <p v-for="(item,key) in lists">
              <span><span class="must">*</span> {{item.name}} <strong>:</strong></span>
              <input type="text" :placeholder="item.place" v-model.trim="item.modal" @focus="emailEnter(key)" @blur="emailOut(key)" @input="emailInput(key)" class="emailInput" :disabled="emailShow">
              <strong v-if="item.show"><!--<Icon type="close-circled" color="#e46751" size="20" style="margin-right: 0.05rem"></Icon>--> {{item.error}} </strong>
              <strong v-if="item.right"> <Icon type="checkmark-circled" size="20" color="green"></Icon> </strong>
            </p>
          </form>
        </div>
    <p class="btn">
      <a href="javascript:void(0)" class="save" @click="save" v-if="!show">保存</a>
      <a href="javascript:void(0)" class="save" @click="changeData" v-if="show">修改</a>
      <a href="javascript:void(0)" @click="cancel"  class="cancel">取消</a>
    </p>
  </div>
</template>
<script>
  import bread from '../bread.vue'
  export default{
    data(){
      return {
        name:'',
        show:false,
        phoneShow:false,
        emailShow:false,
        message:'0',
        email:'0',
        items:[
          {
            name:"短信平台账号",
            place:"请输入短信平台账号",
            error:'请填写短信平台账号',
            modal:'',
            show:null,
            right:false
          },
          {
            name:"短信平台密钥",
            place:"请输入短信平台密钥",
            error:'请填写短信平台密钥',
            modal:'',
            show:null,
            right:false
          },
          {
            name:"短信平台价格",
            place:"请输入短信价格",
            error:'请填写短信价格',
            modal:'',
            show:null,
            right:false
          }
        ],
        lists:[
          {
            name:"邮件服务器",
            place:"请输入邮件服务器",
            error:'请填写邮件服务器',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"服务器端口号",
            place:"请输入服务器端口号",
            error:'请填写服务器端口号',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"邮箱用户名",
            place:"请输入邮箱用户名",
            error:'请填写邮箱用户名',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"邮箱密码",
            place:"请输入邮箱密码",
            error:'请填写邮箱密码',
            modal:'',
            show:false,
            right:false
          }
        ],
      }
    },
    methods:{
      save(){
        if(this.message==='1' && this.email === '1'){
          this.all();
        }else if(this.message==='1' && this.email==='0'){
          this.onlyWx()
        }else if(this.message==='0' && this.email==='1'){
          this.onlyPay();
        }else{
          alert('请开启在保存/修改数据')
        }
      },
      changeData(){
        this.publicAjax(1);
      },
      cancel(){
        if(this.message==='1' && this.email === '1'){
          this.cancelAll();
        }else if(this.message==='1' && this.email==='0'){
          this.cancelWx()
        }else if(this.message==='0' && this.email==='1'){
          this.cancelPay();
        }
      },
      cancelAll(){
        for(let i=0;i<this.items.length;i++){
          this.items[i].show=false;
          this.items[i].right=false;
          this.items[i].modal='';
        }
        let input=document.getElementsByClassName('phoneInput');
        for(let j=0;j<input.length;j++){
          input[j].style.borderColor='#EAEAEA';
        }
        for(let i=0;i<this.lists.length;i++){
          this.lists[i].show=false;
          this.lists[i].right=false;
          this.lists[i].modal='';
        }
        let payInput=document.getElementsByClassName('emailInput');
        for(let j=0;j<payInput.length;j++){
          payInput[j].style.borderColor='#EAEAEA';
        }
      },
      cancelWx(){
        for(let i=0;i<this.items.length;i++){
          this.items[i].show=false;
          this.items[i].right=false;
          this.items[i].modal='';
        }
        let input=document.getElementsByClassName('phoneInput');
        for(let j=0;j<input.length;j++){
          input[j].style.borderColor='#EAEAEA';
        }
      },
      cancelPay(){
        for(let i=0;i<this.lists.length;i++){
          this.lists[i].show=false;
          this.lists[i].right=false;
          this.lists[i].modal='';
        }
        let payInput=document.getElementsByClassName('emailInput');
        for(let j=0;j<payInput.length;j++){
          payInput[j].style.borderColor='#EAEAEA';
        }
      },
      publicAjax(val){
        this.post('system/Config/setSystemSms',{
          appid:this.items[0].modal,
          appsecret:this.items[1].modal,
          sms_money: this.items[2].modal,
          is_mobile:1,
          mail_server:this.lists[0].modal,
          mail_port:this.lists[1].modal,
          mail_user:this.lists[2].modal,
          mail_password:this.lists[3].modal,
          is_mail:1,
          id:val
        }).then(res=>{
          if(res.data.code===10000){
            alert(res.data.msg_test);
          }
        })
      },
      all(val){
        if(this.items[0].modal &&this.items[1].modal&&this.lists[0].modal&&this.lists[1].modal&&this.lists[2].modal&&this.lists[3].modal){
          this.publicAjax(val);
        }else{
          let wxInput=document.getElementsByClassName('phoneInput');
          let payInput=document.getElementsByClassName('emailInput');
          if(!this.items[0].modal){
            wxInput[0].style.borderColor = '#e46751';
            this.items[0].show = true;
          }
          if(!this.items[1].modal){
            wxInput[1].style.borderColor = '#e46751';
            this.items[1].show = true;
          }
          if(!this.lists[0].modal){
            payInput[0].style.borderColor = '#e46751';
            this.lists[0].show = true;
          }
          if(!this.lists[1].modal){
            payInput[1].style.borderColor = '#e46751';
            this.lists[1].show = true;
          }
          if(!this.lists[2].modal){
            payInput[2].style.borderColor = '#e46751';
            this.lists[2].show = true;
          }
          if(!this.lists[3].modal){
            payInput[3].style.borderColor = '#e46751';
            this.lists[3].show = true;
          }
        }
      },
      onlyWx(val){
        if(this.items[0].modal &&this.items[1].modal&&this.items[2].modal&&this.items[3].modal){
          this.publicAjax(val);
        }else{
          let wxInput=document.getElementsByClassName('phoneInput');
          if(!this.items[0].modal){
            wxInput[0].style.borderColor = '#e46751';
            this.items[0].show = true;
          }
          if(!this.items[1].modal){
            wxInput[1].style.borderColor = '#e46751';
            this.items[1].show = true;
          }
        }
      },
      onlyPay(val){
        if(this.lists[0].modal&&this.lists[1].modal&&this.lists[2].modal){
          this.publicAjax(val);
        }else{
          let payInput=document.getElementsByClassName('emailInput');
          if(!this.lists[0].modal){
            payInput[0].style.borderColor = '#e46751';
            this.lists[0].show = true;
          }
          if(!this.lists[1].modal){
            payInput[1].style.borderColor = '#e46751';
            this.lists[1].show = true;
          }
          if(!this.lists[2].modal){
            payInput[2].style.borderColor = '#e46751';
            this.lists[2].show = true;
          }
          if(!this.lists[3].modal){
            payInput[3].style.borderColor = '#e46751';
            this.lists[3].show = true;
          }
        }
      },
      cancelCheck(val){
        this.items[val].show=false;
      },
      check(val){
        if(this.items[val].modal){
          this.items[val].show=false;
          this.items[val].right=true;
          event.target.style.borderColor='#EAEAEA'
        }else{
          this.items[val].show=true;
          this.items[val].right=false;
          event.target.style.borderColor='#e46751'
        }
      },

      enter(){
        event.target.style.borderColor='#6098FF'
      },
      out(val){
        switch (val){
          case 0:
            this.check(0);
            break;
          case 1:
            this.check(1);
            break;
          case 2:
            this.check(2);
            break;
          case 3:
            this.check(3);
            break;
        }
      },
      input(val){
        switch (val){
          case 0:
            this.cancelCheck(0);
            break;
          case 1:
            this.cancelCheck(1);
            break;
          case 2:
            this.cancelCheck(2);
            break;
          case 3:
            this.cancelCheck(3);
            break;
        }
      },
      change(val){
        if(val){
          this.emailShow=!this.emailShow
        }else{
          this.phoneShow=!this.phoneShow;
        }
      },
//      支付宝
      cancelCheckPay(val){
        this.lists[val].show=false;
      },
      checkPay(val){
        if(this.lists[val].modal){
          this.lists[val].show=false;
          this.lists[val].right=true;
          event.target.style.borderColor='#EAEAEA'
        }else{
          this.lists[val].show=true;
          this.lists[val].right=false;
          event.target.style.borderColor='#e46751'
        }
      },
      emailEnter(){
        event.target.style.borderColor='#6098FF'
      },
      emailOut(val){
        switch (val){
          case 0:
            this.checkPay(0);
            break;
          case 1:
            this.checkPay(1);
            break;
          case 2:
            this.checkPay(2);
            break;
            case 3:
            this.checkPay(3);
            break;
        }
      },
      emailInput(val){
        switch (val){
          case 0:
            this.cancelCheckPay(0);
            break;
          case 1:
            this.cancelCheckPay(1);
            break;
          case 2:
            this.cancelCheckPay(2);
            break;
        }
      },
    },
    mounted(){
      this.post("system/Config/getSystemSms",{id:1}).then(res=>{
        if(res.data.code===10000){
          this.message = `${res.data.data.is_mobile}`;
          this.email = `${res.data.data.is_mail}`;
          this.items[0].modal = res.data.data.appid;
          this.items[1].modal =  res.data.data.appsecret;
          this.items[2].modal =  res.data.data.sms_money;
          this.lists[0].modal = res.data.data.mail_server;
          this.lists[1].modal = res.data.data.mail_port;
          this.lists[2].modal = res.data.data.mail_user;
          this.lists[3].modal = res.data.data.mail_password;
          this.phoneShow = this.message === '1' ? false : true;
          this.emailShow = this.email === '1' ? false : true;
        }
      })
    },
     components: {
      bread
    }
  }
</script>
<style scoped>
  .open{
    display: flex;
  }
  .open>div:nth-child(2){
    margin: 0 20px;
  }
  .pay{
    margin-top:20px;
    margin-bottom: 40px;
  }
  .title{
    display: inline-block;
    font-size:16px;
    padding:15px 16px;
    margin-right:20px;
  }
  .box{
    display: flex;
    align-items: center;
  }
  .line{
    border-bottom: 1px solid #eeeeee;
  }
  p{
    padding: 3px 0;
    display: flex;
    align-items: center;
  }
  p>span{
    display: inline-block;
    width: 120px;
    text-align: right;
    margin-right:20px;
  }
  p>strong{
    display: flex;
    align-items: center;
    color: #e46751;
    font-weight: 400;
    margin-left: 10px;

  }
  .must{
    font-size: 15px;
    color: #e46751;
    margin-right:10px;
  }
  input{
    display: inline-block;
    padding: 8px 10px;
    border: 1px solid #EAEAEA;
    width: 400px;
    border-radius:5px;
    background-color: #FFFFFF;
  }
   .btn{
    padding: 0px 140px;
  }
  .save{
    display: inline-block;
    padding: 8px 35px;
    background-color: #6098FF;
    border-radius:5px;
    color: white;
  }
   .save:hover{
    text-decoration: none;
  }
  .cancel{
    margin-left: 15px;
    display: inline-block;
    padding: 7px 34px;
    border: 1px solid #777777;
    border-radius: 5px;
    color:#777777;
  }
  .cancel:hover{
    background:#799EE3 ;
    color: white;
    text-decoration:none;
    border: none;
  }
</style>



